@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;0,900;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;0,900;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#counter {
    display: flex;
    /* width: 300px; */
    /* box-shadow: 1px 5px 10px rgba(189, 189, 189, 0.179);
    border-radius: 5px; */
    width: 360px;
    justify-content: space-evenly;
    align-items: center;
    /* user-select: none; */
}

#counter p{
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
}

#counter h2{
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 800;
}


.dateItem {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-items: center;
    padding: 10px 0
    /* margin-top: 15px; */
    /* background-color: black; */

}

#cover {
    display: flex;
    flex-wrap: wrap;
    background: #F0F2F0;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #000C40, #F0F2F0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    height: 100vh;
}
#tsparticles {
    height: 100%;
    width: 80%;
    background-position: fixed;
    position: fixed;
}

.coverHead {
    padding: 30px 0;
    display: flex;
    flex-wrap: wrap;
    /* background: tomato; */
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

#logo {
    height: 150px;
    width: 350px;
    margin: auto;
    background-image: url(/uab/static/media/logo.211e48e3.png);
    background-size: 400px;
    background-position: center;
    background-repeat: no-repeat;
}

.titleBox {
    width: 300px;
    margin: auto;
}

.titleBox h1 {
    text-align: right;
    color: rgb(0, 0, 0);
    font-family: 'Roboto', sans-serif;
    font-weight: 100;

}

#mainInfo {
    /* background-color: rgba(0, 0, 0, 0.2); */
    /* position: absolute; */
    z-index: 1;
    height: -webkit-min-content;
    height: min-content;
    border-radius: 5px;
    color: white;
    width: 400px;
    margin: 30px auto;
    /* margin-left: 12%; */

}


.infoBox {
    display: flex;
    justify-content: space-around;
    /* background-color: yellow; */
    padding: 10px;
    
}

.features {
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: orange; */
    margin: 10px;
}

.features p{
    margin: 0 5px;
}

.counter {
    color: white;
    margin: auto;
}

#subscribeButton {
    width: 100%;
    height: 50px;
    background-color: transparent;
    border-radius: 5px;
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.22);
    transition: 100ms;
}

#subscribeButton:hover {
    background-color: #f5f5f5;
    color: black;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

